import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

import Frame1280pxPortfoliotemplate from './views/frame1280px-portfoliotemplate'
import NotFound from './views/not-found'
import './style.css'

Vue.use(Router)
Vue.use(Meta)
export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'Frame1280pxPortfoliotemplate',
      path: '/',
      component: Frame1280pxPortfoliotemplate,
    },
    {
      name: '404 - Not Found',
      path: '**',
      component: NotFound,
      fallback: true,
    },
  ],
})
